import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import ReactMarkdown from "react-markdown";
import rehypeRaw from "rehype-raw"; 
import Header from "../components/Header";
import Footer from "../components/Footer";

export default function QueryPage() {
  const { uuid } = useParams();
  const [markdown, setMarkdown] = useState("");
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!uuid) return;
    setLoading(true);

    axios.get(`https://api.earmiss.ru/summaries/${uuid}`)
      .then(res => {
        // убедись, что сервер возвращает просто Markdown текст
        // например: "# Заголовок\n\nТекст"
        setMarkdown(res.data.message|| ""); 
      })
      .catch(err => {
        console.error(err);
        setMarkdown("");
      })
      .finally(() => setLoading(false));
  }, [uuid]);

  return (
    <div className="bg-[#FFF8E7] ">
    <Header />
    <div className="antialiased text-gray-800 min-h-screen flex flex-col">
      <main className="flex-grow max-w-4xl mx-auto px-6 py-12 w-full">
        {loading && <p>Загрузка...</p>}
        {!loading && markdown && (
          <div className="max-w-4xl mx-auto p-8 bg-white/80 backdrop-blur-md rounded-2xl shadow-lg border border-gray-200">
            <div className="prose prose-sm sm:prose-base md:prose-lg max-w-none
                            prose-h1:text-2xl prose-h2:text-xl prose-h3:text-lg prose-h4:text-base">
              <ReactMarkdown rehypePlugins={[rehypeRaw]}>
                {markdown}
              </ReactMarkdown>
            </div>
          </div>
        )}
        {!loading && !markdown && <p>Нет данных для отображения.</p>}
      </main>
    </div>
    <Footer />
    </div>
  );
}
